
.grids-container {
    align-items: flex-start;
}

.modules.playing {
    //z-index: 20000;
}

.modules {
    z-index: 20000;
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    top: 25%;
    left: 30%;
    // max-width: 0px !important;

    .plane-btn {
        display: none;
    }

    .stat-number {
        display: none;
    }
    .module-inner {
        background-color: #00000047;
        margin-right: 10px;
        border: 1px dashed #ffffff2e;
        
        .effect {
            //width: 0px;
            //height: 0px;
            min-width: unset !important;
            .plane-toolbar {
                display: none !important;
            }
            .plane-container {
                svg {
                    transform: scale(4);
                                        
                    path {
                        opacity: 0.2;
                    }
                    circle {
                        opacity: 0.5;
                    }
                }
            }
            .snapshot-toolbar {
                display: none;
            }
            .plane-group-buttons {
                margin-left: 0px !important;
            }
            .x-axis, .y-axis {
                display: none;
            }
            padding-bottom: unset !important;
            .effect-header {
                display: none !important;
            }
        }
    }
}

.collapsed.grids, .modules {
    padding-left: unset !important;
    align-items: flex-start;
}

.collapsed.grids, .modules, .z-object {
    .module-inner {
        
        .direction-parameter, .offset-parameter {
            display: none !important;
        }
        
        .script-box {
            width: 370px !important;
        }
        .note-display {
            .note {
                height: 57px;
                width: 13px;
            }
        }
        .module-row, .module-col {
            display: flex;
        }

        .module-col {
            flex-direction: column;
        }

        .pad-inner {
            padding: 0px;
            position: relative;
            margin-top: 0px;
            margin-right: 0px;

            .pad-slot.selected {
                box-shadow: unset;
            }

            .recording-dot {
                display: none !important;
                pointer-events: none !important;
            }

            .pad-slots {
                width: 170px;
                padding-left: 10px;
                .pad-slot {
                    .sound-thumbnail {
                        pointer-events: none;
                        letter-spacing: unset !important;
                        padding-left: 2px;
                        font-size: 5pt;

                    }
                    margin: 2px;
                    width: 25px;
                    height: 25px;
                }
            }
            .collapse-container, .pad-toolbar {
                display: none;
            }
        }
        .effect.Playhead {
            height: unset !important;
            width:  202px !important;
        }
        .effect {
            box-shadow: unset !important;
        }
        .effect {
            margin: 0px;
            .effect-header, .bottom-effect-toolbar {
                display: none;
            }
        }
        .starred-effects {
            .documented:first-child {
                display: flex;
                flex-direction: column;
            }
            .pulled-sequencer {
                position: relative;
                top: unset !important;
                left: unset !important;
                .output-node, .delete-track-trash {
                    display: none;
                }
            }
            .plane-parameters {
                .plane-group-config {
                    .plane-algo {
                        display: flex;
                    }
                }
                .plane-sidebar, .script-box, .script-toolbar, .expanded-icon, .plane-algo, .control-toolbar {
                    display: none;
                }
            }
            .effect.collapsed {
                padding-bottom: unset;
                .effect-from {
                    margin-right: 35px;
                }

                .effect-name, .effect-from {
                    text-align: unset;
                    font-size: 9pt;
                    margin-top: unset;
                    margin-bottom: unset;
                    writing-mode: unset;
                }
            }

            .effect {
                margin-top: 2px;
                position: relative;
                padding-bottom: 25px;
                .effect-header {
                    position: absolute;
                    bottom: 0px;
                    display: flex;
                    right: 0px;
                    .collapse-toggle {
                        margin-top: 2px;
                        border-width: 1px;
                        .arrow-right, .arrow-down {
                            margin-left: 3px;
                            margin-top: 2px;
                        }
                    }
                    .effect-toggle, .delete-button {
                        display: none;
                    }
                }
                min-width: 382px;
            }
        }
    }
}

.grids.not-collapsed {
    transform-origin: top left;
    animation-duration: 0.4s;
    animation-name: grow-module;
}

@keyframes shrink-module{
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}

@keyframes grow-module{
    from{
        opacity: 0;
    }

    to{
        opacity: 1;
    }
}

.module-tab.selected {
    border-bottom: 2px solid white;
}

.module-tab {
    border-bottom: 2px solid transparent;
    display: flex;
    padding-left: 10px !important;

    .patterns-container {
        float: none;
        margin-top: -8px;
    }
    
    .collapse-button {
        cursor: pointer;
        position: absolute;
        top: 8px;
        right: 4px;

        .arrow-left {
            border-bottom: 6px solid transparent;
            border-top: 6px solid transparent;
            border-right: 14px solid white;
        }
        .arrow-down {
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-top: 14px solid white;
        }
    }
}

.color-btn.showing {
    box-shadow: 0px 0px 3px 3px white;
}
.color-btn {
    width: 15px;
    height: 15px;
    border-radius: 15px;
    margin-top: 5px;
    margin-left: 5px;
    cursor: pointer;
}

.chrome-picker {
    top: 30px;
    left: 30px;
    position: absolute;
    z-index: 100000000000000000000000;
}


.zobject-toolbar {
    position: relative;
    margin-left: 25px;

    .toolbar-tooltip {
        position: absolute;
        z-index: 100000000000000000;
        top: 45px;
        display: flex;
        min-width: 200px;
        background-color: #0a0a0acf;
        color: white;
        padding: 5px;
        font-size: 9pt;
    }

    .modulator-expansion {
        position: absolute;
        top: 30px;
        left: 250px;
        padding: 10px;
        z-index: 10000000000;
        background-color: #0a0a0acf;
        display: flex;

        .modulator-type {
            margin-right: 15px;
        }

        .modulator-number {
            margin-top: 5px;
            background-color: #2b2b2b;
            font-size: 8pt;
            margin-right: 5px;
            width: 20px;
            height: 20px;
            text-align: center;
            cursor: pointer;
            line-height: 20px;
        }

    }
    
    img:hover, img.selected {
        opacity: 1;
    }
    
    img.expanded {
        opacity: 1;
    }

    img {
        opacity: 0.4;
        width: 20px;
        cursor: pointer;
    }
}


