.module-tab.selected {
    position: fixed;
}

.MIX_VIEW {
    .mix-tab {
        display: none !important;
    }
    .mix-view.mixes {
        .grids.collapsed {
            .module-tab.selected, .grids-name.selected {
                position: relative;
            }
        }
        .grids {
            margin-top: 0px !important;
            margin-left: 0px !important;
            .module-tab.selected, .grids-name.selected {
                position: fixed;
                left: 0px;
                z-index: 1000000000;
                background-color: #0a0a0a;
                
            }
        }
    }
}
