.explore-2 { 
    .explore-work {
        display: flex;
        flex-wrap: wrap;
        flex: 1;
        overflow-y: scroll;

        .work-save-button {
            right: 200px;
            background-image: url(/floppy-disk.svg);
            background-size: cover;
            width: 60px;
            height: 60px;
        }

        .combine-works-button {
            right: 300px;
            background-color: #63ea86;
            color: black;
        }

        .combine-works-button, .work-save-button {
            position: fixed;
            bottom: 40px;
            font-size: 16pt;
            padding: 5px;
            cursor: pointer;
        }
    }
}

.explore-2, .mix-view {
    .grids {
        margin: 25px;
        position: relative;
        padding-left: 5px;

        .grid-row {
            display: flex;
            
            .grid.disabled {
                opacity: 0.2;
            }
            
            .grid.pressed {
                background-color: #6579c3;
                box-shadow: 0px 0px 6px 6px #98d6e494;
                
                .volume-indicator {
                    background-color: #d1ceff;
                    opacity: 0.4;
                }
            }
            
            .grid {
                cursor: pointer;
                margin: 10px;
                height: 60px;
                z-index: 4;
                width: 60px;
                background-color: #212121;
                padding: 0px;
                position: relative;
                
                .volume-indicator.skinny {
                    width: 4px;
                    right: 0px;
                }
                .volume-indicator {
                    position: absolute;
                    width: 60px;
                    bottom: 0px;
                    background-color: #abbff7;
                    opacity: 0.7;
                }


                
                .youtube-info {
                    margin-left: 0px;
                    .youtube-thumbnail {
                        margin-left: 0px;
                        height: 20px;
                    }
                }
            }
        }
    }
}

