.months {
    margin-left: auto;
    background-color: #00000063;
    backdrop-filter: blur(3px);
    backface-visibility: hidden;
    
    .month.current {
        color: white;
    }

    .month.year {
        letter-spacing: 8px;
        color: #68676d;
    }

    .month {
        cursor: pointer;
        font-size: 14pt;
        margin: 15px;
        color: gray;
    }
}

