.module-overview {
    position: absolute;
    padding: 10px;
    background-color: #1f1f1f5e;
    border-radius: 10px;
    z-index: 20000;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .tr-overview.selected {
        .track-number, .right-arr {
            opacity: 1;
        }
    }

    .tr-overview {
        align-items: flex-start;
        display: flex;
        flex-direction: column;
        .tr-inner {
            display: flex;
        }

        .effects-overview {
            display: flex;
            margin-top: 10px;
            .Dropdown-control {
                border-color: black;
                padding: 4px;
                width: 100px;

            }
            .Dropdown-arrow {
                top: 8px;
            }
        }

        .instrument-overview {
            margin-top: 10px;
        }

        font-size: 8pt;
        background-color: #3e3e3e91;
        margin-bottom: 4px;
        padding: 8px;

        .patt {
            background-color: #21FFE4;
            color: black;
        }

        .a-fx, .inst, .m-fx {
            cursor: pointer;
        }

        .m-fx {
            background-color: black;
        }

        .showing {
            box-shadow: 0px 0px 4px 4px white;
        }

        .inst {
            background-color: #FF6341;
            width: 45px;
            text-align: center;
            overflow: hidden;
            white-space: nowrap;
            color: black;
        }

        .a-fx {
            background-color: #4E10FF;
            color: black;
        }

        .dot {
            width: 12px;
            margin-top: 3px; 
            height: 12px;
            margin-left: 5px;
            margin-right: 5px;
            background-color: gray;
            cursor: pointer;
        }

        .dot.enabled {
            background-color: red;
        }

        .track-number, .patt, .m-fx, .inst, .a-fx {
            padding-left: 8px;
            padding-right: 8px;
            margin-left: 5px;
            height: 18px;
            line-height: 18px;
            
            white-space: nowrap;
        }

        .track-number {
            margin-left: -8px;
            width: 13px;
            color: white;
            opacity: 0.3;
        }

        .right-arr {
            opacity: 0.3;
            pointer-events: none;
            width: 9px;
            margin-left: 4px;
        }
    }
}
